<template>
  <div>
    <div class="adiv">
      <van-icon name="arrow-left" class="myicon" @click="$router.back()" />
      <h1>文章详情</h1>
    </div>
    <div class="guding">
      <div class="list">
        <h1>{{ list?.title }}</h1>
        <div class="title">
          <div class="myimg">
            <img @error="myerror" :src="list?.avatar" alt="" />
          </div>
          <div class="info">
            <h2>{{ list?.nickname }}</h2>
            <p>
              <span>
                {{
                  new Date(parseInt(list?.time))
                    .toLocaleString()
                    .replace(/:\d{1,2}$/, "")
                }}</span
              >
            </p>
          </div>

          <div class="guanzhu" @click="unadd" v-if="list?.is_fav">已关注</div>
          <div class="guanzhu1" v-else @click="add">+关注</div>
        </div>
        <van-divider
          :style="{ color: myColor, borderColor: myColor, padding: '0 16px' }"
        >
          内容
        </van-divider>
        <div class="msg">
          <p>{{ list?.content }}</p>

          <img
            :src="myimg"
            class="imgmy"
            alt=""
            @click="btn(myimg)"
            v-for="myimg in list?.imageSrc"
          />
        </div>
      </div>
      <div class="pinglun">
        <div class="tit">
          <h6>评论{{ list?.comment }}</h6>
          <p>
            <span>{{ list?.like }}赞</span>||<span>转发{{ list?.fav }}</span>
          </p>
        </div>
        <div class="content" v-for="item in pllist" :key="item.id">
          <div class="tit">
            <div class="mytouxiang">
              <van-image class="touxiang" round :src="item.info.avatar" />
            </div>
            <h5>{{ item.info.nickname }}</h5>
            <p>
              <van-icon
                name="good-job"
                v-if="item.is_like"
                :color="myColor"
                @click="ungood(item._id)"
              />

              <van-icon name="good-job-o" v-else @click="good(item._id)" />
              {{ item.like_count }}
            </p>
          </div>
          <div class="con">
            <h3>{{ item.content }}</h3>
            <p>{{ new Date(parseInt(item.create_time)).toLocaleString() }} <button @click="huifuta(item._id)">回复他</button></p>
          </div>
          <div class="huifu">
            <div class="huifulist" v-for="item1 in huifu"  v-if="item._id==item1.reply_comment_id">
              <div class="yi"  >
                <img :src="item1.info.avatar" alt="">
                <li>{{item1.info.nickname}}>>回复>>{{ item.info.nickname }}</li>
              </div>
              <div class="er">
                <li>{{item1.content}}</li>
                <p>{{timeAgo(item1.create_time)}}</p>
              </div>
            </div>
            <p @click="zhankai(item._id)">{{nonetext}}</p>
          </div>
        </div>
      </div>
    </div>
    <div class="fbottom">
      <input type="text" name="" id="" placeholder="善于结善缘，恶言伤人心~~" v-model="pinglun" @keydown.enter="publish()"/>
      <van-icon
        name="comment-o"
        :badge="list.comment"
        @click="publish"
      />
      <van-icon
        name="like"
        :color="myColor"
        @click="unlike"
        v-if="list.is_like"
        :badge="list.like"
      />

      <van-icon name="like-o" @click="like" :badge="list.like" v-else />
      <van-icon
        name="star"
        :color="myColor"
        @click="unadd"
        v-if="list.is_fav"
        :badge="list.fav"
      />

      <van-icon name="star-o" @click="add" v-else :badge="list.fav" />
      <van-icon name="share-o" @click="showShare = true" />
    </div>

    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
      @select="onSelect"
    />
  </div>
</template>

<script>
import {
  detaildeAPI,
  commentAPI,
  likeAPI,
  unlikeAPI,
  add_favAPI,
  remove_fav,
  add_comment,
  comment_like,
  comment_unlike,
  get_reply_list,
} from "@/api/detailed";
import { ImagePreview } from "vant";
import { timeago } from "@/assets/time";
 
export default {
  name: "detailed",
  components: {
    [ImagePreview.Component.name]: ImagePreview.Component,
  },
  data() {
    return {
      showShare: false,
      options: [
        { name: "微信", icon: "wechat" },
        { name: "微博", icon: "weibo" },
        { name: "复制链接", icon: "link" },
        { name: "分享海报", icon: "poster" },
        { name: "二维码", icon: "qrcode" },
      ],
      item_id: this.$route.query.item_id,
      cate_id: this.$route.query.cate_id,
      list: [],

      pllist: [],
      pinglun: "",
      huifu:[],
      loginfo: JSON.parse(sessionStorage.getItem("loginfo")),
       none:false,
       nonetext:"——————展开回复",
       dangqian:[],
       type:0
    };
  },
  methods: {
    huifuta(id){
       add_comment({
        user_id: this.loginfo.uid,
        article_id: this.item_id,

        content: this.pinglun,
        reply_comment_id: id,
        comment_type: 1,
      })
        .then((result) => {
          this.$toast("回复成功了");
          this.$notify({ type: "danger", message: "回复成功了" });
          this.comment();
          if (result.data.msg == "评论成功") {
            this.pinglun = "";
            
          }
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    timeAgo(val) {
      return timeago(val);
    },
    zhankai(id) {
      console.log('id: ', id);
            get_reply_list({
        skip: 0,
        limit: 10,
        user_id:this.loginfo._id,
        reply_comment_id:id,
        article_id:this.item_id,
      })
        .then((result) => {
         
          this.huifu=result.data.data
          if(this.huifu.length==0){
               this.$toast("我没有评论回复哦");
          this.$notify({ type: "danger", message:"我没有评论回复哦" }); 
          }
        
    

        })
        .catch((err) => {
          console.log('err: ', err);

        });
  //   this.dangqian=[]

  //  let a =this.huifu.filter((v)=>{
  //         return v.reply_comment_id==id
  //   })
  //   this.dangqian=a
  //     console.log('id: ', this.dangqian);

    },
    btn(myimg) {
      ImagePreview([myimg]);
    },
    myerror(e) {
      e.target.src = require("../assets/error.png");
    },
    onSelect(option) {
      this.$toast(option.name);
      this.showShare = false;
    },
    comment() {
      commentAPI({
        article_id: this.item_id,
        skip: 0,
        limit: 100,
        user_id: this.cate_id,
      })
        .then((result) => {
          console.log("result: ", result);
          this.pllist = result.data.data;

      //     for (let index = 0; index < this.pllist.length; index++) {
      //       get_reply_list({
      //   skip: 0,
      //   limit: 10,
      //   user_id:this.loginfo._id,
      //   reply_comment_id:this.pllist[index]._id,
      //   article_id:this.item_id,
      // })
      //   .then((result) => {
      //     console.log('result: ', result);
        
      //     this.huifu = this.huifu.concat(result.data.data);

      //   })
      //   .catch((err) => {
      //     console.log('err: ', err);

      //   });
            
      //     }
             

        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    detailde() {
      detaildeAPI({
        article_id: this.item_id, //文章ID
        user_id: this.cate_id, //用户ID
      })
        .then((result) => {
          this.list = result.data.data;
          this.islike = result.data.data.islike;
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    publish() {
      add_comment({
        user_id: this.loginfo.uid,
        article_id: this.item_id,

        content: this.pinglun,
        reply_comment_id: "",
        comment_type: 0,
      })
        .then((result) => {
          this.$toast(result.data.msg);
          this.$notify({ type: "danger", message: result.data.msg });
          this.comment();
          if (result.data.msg == "评论成功") {
            this.pinglun = "";
            
          }
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    like() {
      likeAPI({ user_id: this.loginfo.uid, article_id: this.item_id })
        .then((result) => {
          this.$toast(result.data.msg);
          this.$notify({ type: "danger", message: result.data.msg });
          this.detailde();
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    unlike() {
      unlikeAPI({ user_id: this.loginfo.uid, article_id: this.item_id })
        .then((result) => {
          this.$toast(result.data.msg);
          this.$notify({ type: "danger", message: result.data.msg });
          this.detailde();
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    add() {
      add_favAPI({ user_id: this.loginfo.uid, article_id: this.item_id })
        .then((result) => {
          this.$toast(result.data.msg);
          this.$notify({ type: "danger", message: result.data.msg });
          this.detailde();
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    unadd() {
      remove_fav({ user_id: this.loginfo.uid, article_id: this.item_id })
        .then((result) => {
          this.$toast(result.data.msg);
          this.$notify({ type: "danger", message: result.data.msg });
          this.detailde();
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    good(comment_id) {
      comment_like({
        user_id: this.cate_id, //用户ID
        comment_id: comment_id, //点击的那条评论的ID
      })
        .then((result) => {
          this.$toast(result.data.msg);
          this.$notify({ type: "danger", message: result.data.msg });
          this.comment();
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    ungood(comment_id) {
      comment_unlike({
        user_id: this.cate_id, //用户ID
        comment_id: comment_id, //点击的那条评论的ID
      })
        .then((result) => {
          this.$toast(result.data.msg);
          this.$notify({ type: "danger", message: result.data.msg });
          this.comment();
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
  },
  created() {
    this.detailde();
    this.comment();
  },
};
</script>

<style lang="scss" scoped>
.huifulist{
  height: 50px;
  display: flex;
  flex-direction: column;
  .yi{
    display: flex;
    align-items: center;
    li{
      margin-left: 10px;font-size: 12px;
    }
    img{
      width:30px;height: 30px;
      border-radius: 20px;
    }
  }
  .er{
    display: flex;
        justify-content: space-evenly;
    align-items: center;
     li{
      margin-left: 10px;font-size: 12px;
    }
  }
  
}
.guding {
  margin-bottom: 60px;
}
.adiv {
  width: 100vw;
  height: 50px;
  background-color: $mycolor;
  display: flex;
  align-items: center;
  color: aliceblue;
  font-size: 14px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}
h1 {
  margin: auto;
  font-size: 16px;
  word-wrap: break-word;
}
.myicon {
  font-size: 32px;
}
.list {
  width: 100vw;
  margin-top: 60px;

  h1 {
    font-size: 30px;
    padding: 20px;
  }
  h2 {
    font-size: 24px;
  }
  .title {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100vw;
    height: 60px;

    .myimg {
      width: 50px;
      height: 50px;
      img {
        width: 50px;
        height: 50px;
        border-radius: 50px;
      }
    }
    .info {
      display: flex;
      width: 200px;
      height: 60px;
      justify-content: space-between;
      flex-direction: column;
      h2{
          width: 200px;
          overflow: hidden;
          text-overflow: ellipsis;
  // word-break: break-all;
      }
      p {
        font-size: 16px;
        color: #777171;
      }
    }
    .guanzhu {
      width: 70px;
      color: white;
      line-height: 30px;
      text-align: center;
      height: 30px;
      background: $mycolor;
      font-size: 16px;
      border-radius: 10px;
    }
    .guanzhu1 {
      width: 70px;
      color: $mycolor;
      line-height: 30px;
      text-align: center;
      height: 30px;
      background: white;
      font-size: 16px;
      border: 1px solid $mycolor;
      border-radius: 10px;
    }
  }
  .msg {
    padding: 20px;
    line-height: 20px;
    text-indent: 20px;
    font-size: 16px;
    p {
      word-wrap: break-word;
    }
    .imgmy {
      max-width: 100%;
      display: block;
      border-radius: 10px;
      margin: 8px auto 8px;
    }
  }
}
.fbottom {
  width: 100vw;
  height: 50px;
  display: flex;
  align-items: center;
  padding: 0 15px;
  position: fixed;
  bottom: 0px;
  left: 0;
  background: #ededed;

  input {
    border: 1px solid #ededed;
    border-radius: 15px;
    padding: 6px 8px;
    font-size: 14px;
    text-align: center;
  }

  .van-icon {
    padding: 5px;
    margin: 0 5px;
    font-size: 20px;
  }
}
.pinglun {
  width: 100vw;

  .tit {
    padding: 20px;
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    h6 {
      font-size: 24px;
    }
    p {
      font-size: 16px;
      color: #777171;
    }
  }
}
.content {
  width: 100vw;
  .tit {
    display: flex;
    .mytouxiang {
      width: 50px;
      height: 50px;
      img {
        width: 100%;
        border-radius: 100px;
      }
    }
    h5 {
      width: 60vw;
      font-size: 18px;
    }
  }
  .con {
    width: 70vw;
    margin-left: 80px;
    padding: 5px;
  }
  h3 {
    font-size: 16px;
  word-break: break-all;

    
  }
  p {
    font-size: 12px;
    color: #777171;
    margin-top: 5px;
  }
  .huifu {
    font-size: 5px;
    margin-left: 100px;
  }
}
</style>

